body {
	background-color: #313131;
}

h1 {
	font-family: Chalkduster, fantasy;
	text-align: center;
	color: #fccbcb;
	word-spacing: 20px;
}

h3 {
	font-family: Chalkduster, fantasy;
	text-align: center;
	color: #fccbcb;
}

.inputContainer {
	padding: 30px;
	text-align: center;
	position: relative;
}

/* .top-container {
	position: absolute;
	margin: auto;
} */

.container {
	margin: 3px;
	width: 500px;
	height: 50px;
	position: relative;
	border: 3px solid #525252;
	transform: translate(90%);
	background-color: #414141;
}

input {
	margin-right: 10px;
	width: 25.5%;
	padding: 10px 15px;
	border: none;
	border-bottom: 3px solid #525252;
	background-color: #313131;
	color: #fccbcb;
	font-family: Chalkduster, fantasy;
	font-size: 18px;
}

button {
	padding: 13px;
	border: 30px #525252;
	background-color: #414141;
	font-family: Chalkduster, fantasy;
	font-size: 18px;
}

textarea:focus,
button:focus,
input:focus {
	outline: none;
}
